<template>
  <AppModal id="create-modal-container" v-if="visible">
    <template slot="header">{{ chrome.i18n.getMessage('NEW_FOLDER_TEXT') }}</template>
    <div class="app-modal-content" @keyup.esc="toggle">
      <div class="form-control" :class="{ error }">
        <label class="folder-name-text" for="folder-name">{{ chrome.i18n.getMessage('FOLDER_NAME') }}</label>
        <input
          id="folder-name-input"
          type="text"
          name="folder-name"
          :placeholder="chrome.i18n.getMessage('FOLDER_NAME_PLACE_HOLDER')"
          v-model.trim="folder.title"
          @keyup.enter="confirm"
          @focus="clearErrors"
          v-focus
        />
        <div class="error-text" v-if="error">
          <span>{{ error }}</span>
        </div>
      </div>
      <div class="form-control">
        <label class="storage-location-text" for="folder-parent">{{ chrome.i18n.getMessage('LOCATION_TEXT') }}</label>
        <span class="folder-parent">{{ folder.parentTitle }}</span>
      </div>
    </div>
    <div class="app-modal-controls create-modal">
      <button class="primary addTxt create-modal" @click="confirm">{{ chrome.i18n.getMessage('DREAM_BR_OPT_ADD') }}</button>
      <button class="create-modal modal-button-cancel" @click="toggle">{{ chrome.i18n.getMessage('CANCEL') }}</button>
    </div>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';

export default {
  name: 'CreateModal',
  components: {
    AppModal,
  },
  data() {
    return {
      parentFolder: {},
      folder: {
        title: '',
        parent: '0',
        isFolder: true,
      },
      visible: false,
      error: '',
    };
  },
  mounted() {
    window.CreateModal = this;
  },
  methods: {
    toggle() {
      this.folder.title = '';
      this.visible = !this.visible;
    },
    async confirm() {
      if (!this.folder.title) {
        this.error = FOLDER_NAME_NEEDED;
        return;
      }

      const doesSimilarExist = await checkSimilarBookmark(this.folder);

      if (doesSimilarExist) {
        this.error = FOLDER_NAME_EXISTS;
        return;
      }

      createFolder({
        title: this.folder.title,
        parent: this.folder.parent,
      })
        .then((createdFolderId) => {
          addNodeNewFolder(this.parentFolder, { title: this.folder.title, id: createdFolderId });
          doBookmarkSync();
        })
        .finally(() => {
          this.toggle();
        });
    },
    clearErrors() {
      this.error = '';
    },
  },
  watch: {
    visible(isVisible) {
      storageLocalApi.setItem(IS_MODAL_OPENED, isVisible ? 'true' : 'false');
      if (!isVisible) {
        this.error = '';
      }
    },
  },
};
</script>
