<template>
  <div class="leaf">
    <div :class="{ 'leaf-title': true, selected: selected, selectDisabled: preventSelect }" @click="selectLeaf" @dblclick="toggle">
      <svg class="icon" fill="#000000" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg" @click="toggle" v-if="expanded">
        <path d="M7.41 7.84L12 12.42l4.59-4.58L18 9.25l-6 6-6-6z" />
        <path d="M0-.75h24v24H0z" fill="none" />
      </svg>
      <svg fill="#000000" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg" class="icon" @click="toggle" v-else>
        <path d="M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z" />
        <path d="M0-.25h24v24H0z" fill="none" />
      </svg>
      <span class="folder-icon open" v-if="expanded"></span>
      <span class="folder-icon close" :class="{ disabled: !leaf.children || leaf.children.length == 0 }" v-else></span>
      <div class="title-info">
        <span class="leaf-title-text">{{ leaf.title }}</span>
      </div>
    </div>
    <div class="child-container" v-show="expanded">
      <leaf v-for="child in leaf.children" :key="child.id" :leaf="child" />

      <!-- New Folder -->
      <div v-if="showNewFolder" class="tree-new-folder">
        <svg fill="#000000" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg" class="icon">
          <path d="M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z" />
          <path d="M0-.25h24v24H0z" fill="none" />
        </svg>
        <span class="folder-icon open"></span>
        <input type="text" :class="{ error }" v-model.trim="newFolderTitle" @keydown.enter="addNewFolderByEvent" @blur="addNewFolderByEvent" @input="onChangeFolderName" v-focus />
        <div class="error-text" v-if="error">
          <span>{{ error }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'leaf',
  props: ['leaf'],
  data() {
    return {
      expanded: false,
      selected: false,
      showNewFolder: false,
      newFolderTitle: chrome.i18n.getMessage('NEW_FOLDER_TEXT'),
      error: '',
      preventSelect: false,
    };
  },
  mounted() {
    if (parseInt(this.leaf.id) === parseInt(ROOT_FOLDER_ID)) {
      this.expanded = true;
    }

    EventBus.$on('selectLeaf', (id) => {
      // FIXME: Standardize id to be String
      if (parseInt(this.leaf.id) === parseInt(id)) {
        this.selected = true;

        // Recursively expand ancestors
        let node = this.$parent;
        while (node.$options.name === 'leaf') {
          node.expanded = true;
          node = node.$parent;
        }
      } else {
        this.selected = false;
      }
    });

    EventBus.$on('firstSelectLeaf', (id) => {
      // FIXME: Standardize id to be String
      if (parseInt(this.leaf.id) === parseInt(id)) {
        this.selected = true;

        // Recursively expand ancestors
        let node = this.$parent;
        while (node.$options.name === 'leaf') {
          node.expanded = true;
          node = node.$parent;
        }
      } else {
        this.selected = false;
      }
    });

    EventBus.$on('targetLeaf', (bookmarks) => {
      const targetBookmarks = bookmarks ? (Array.isArray(bookmarks) ? bookmarks : [bookmarks]) : [];
      const targetIds = targetBookmarks.map((bookmark) => (bookmark.data ? bookmark.data.id : bookmark.id));
      // prevent moving bookmark folder into itself and it's children
      if (targetIds.indexOf(this.leaf.id) >= 0) {
        this.preventSelect = true;
      } else {
        this.preventSelect = false;
      }
    });

    EventBus.$on('showNewFolder', async (id) => {
      this.selected = false;
      if (parseInt(this.leaf.id) === parseInt(id)) {
        // Set NewFolder name based on existing folders
        const folders = await getSubFolders(this.leaf.id);
        this.newFolderTitle = getFolderName(folders, chrome.i18n.getMessage('NEW_FOLDER_TEXT'));

        this.showNewFolder = true;
        this.expanded = true;

        EventBus.$emit('isShowNewFolder', true);
      }
    });
  },
  methods: {
    toggle() {
      if (this.leaf.children && this.leaf.children.length) {
        this.expanded = !this.expanded;
      }
    },
    selectLeaf() {
      if (this.leaf.id != IMPORTED_MAIN_FOLDER_IDS.BOOKMARKS) {
        this.toggle();
        EventBus.$emit('selectLeaf', this.leaf.id);
      }
    },
    async addNewFolderByEvent(e) {
      if (e.relatedTarget && e.relatedTarget.className && e.relatedTarget.className.indexOf('modal-button-cancel') >= 0) {
        EventBus.$emit('isShowNewFolder', false);
        return;
      }
      const newFolderInfo = {
        parent: this.leaf.id,
        title: this.newFolderTitle,
        isFolder: 1,
      };
      const doesSimilarExist = await checkSimilarBookmark(newFolderInfo);

      if (doesSimilarExist) {
        this.error = chrome.i18n.getMessage('FOLDER_NAME_EXISTS');
        return;
      }
      this.addNewFolder().then((id) => {
        EventBus.$emit('selectLeaf', id);
        EventBus.$emit('isShowNewFolder', false);
        newFolderInfo.id = id;
        EventBus.$emit('newFolderInfo', newFolderInfo);
      });
    },
    onChangeFolderName() {
      this.error = '';
    },
    async addNewFolder() {
      if (!this.showNewFolder) return;

      const folders = await getSubFolders(this.leaf.id);
      this.newFolderTitle = getFolderName(folders, this.newFolderTitle ? this.newFolderTitle : chrome.i18n.getMessage('NEW_FOLDER_TEXT'));

      const id = await createFolder({
        title: this.newFolderTitle,
        parent: this.leaf.id,
      });

      // FIXME: Avoid prop mutation
      this.leaf.children = this.leaf.children || [];

      this.leaf.children.push({
        id,
        title: this.newFolderTitle,
        parent: this.leaf.id,
        children: [],
      });

      this.showNewFolder = false;

      return new Promise((resolve) => resolve(id));
    },
  },
};
</script>
