<template>
  <div v-show="visible">
    <div class="header-area">
      <div class="content-header">
        <div class="content-title">
          <div class="headerButton back" tabindex="0" @click="hide()">
            <figure>
              <img class="header-back-icon opacity-70" src="/assets/img/internet_pc_ic_back.svg" />
            </figure>
          </div>
          <span class="viewFolderName" :title="chrome.i18n.getMessage('DREAM_BR_TMBODY_USERNAMES_AND_PASSWORDS')">{{
            chrome.i18n.getMessage('DREAM_BR_TMBODY_USERNAMES_AND_PASSWORDS')
          }}</span>
        </div>
      </div>
    </div>
    <div class="app-ready" v-if="isSamsungPassSecured">
      <div class="pass-setting-switch">
        <div class="save-user-info">
          <div class="switchButton" v-if="visible" @click.stop>
            <app-switch-button v-model="saveSigninAndAutoSignin" @onChange="onChangeUsernameAndPasswordSettings"></app-switch-button>
          </div>
        </div>
      </div>
      <span class="userPassSettingText">{{ chrome.i18n.getMessage('DREAM_BR_BODY_AUTOMATICALLY_SIGN_IN_TO_WEBSITES_USING_SAVED_USERNAMES_AND_PASSWORDS') }}</span>
      <div class="pass-setting-options">
        <div class="save-sign-info">
          <span class="saveSignInInfoText">{{ chrome.i18n.getMessage('DREAM_BR_TMBODY_SAVE_SIGN_IN_INFO') }}</span>
          <div class="switchButton" v-if="visible" @click.stop>
            <app-switch-button v-model="saveSignin" @onChange="onChangeSaveSigninSettings"></app-switch-button>
          </div>
        </div>
        <hr class="itemSettingDivider" />
        <div class="auto-sign">
          <span class="autoSignInText">{{ chrome.i18n.getMessage('DREAM_BR_TMBODY_AUTOFILL_SIGN_IN_INFO') }}</span>
          <div class="switchButton" v-if="visible" @click.stop>
            <app-switch-button v-model="autoSignin" @onChange="onChangeAutoSigninSettings"></app-switch-button>
          </div>
        </div>
      </div>
      <div class="samsung-pass">
        <span class="spass-label">{{ chrome.i18n.getMessage('DREAM_BR_HEADER_SAMSUNG_PASS') }}</span>
        <div class="view-in-spass">
          <span class="view-signin cursor-pointer" @click="openSamsungPassApp">{{ chrome.i18n.getMessage('DREAM_BR_TMBODY_VIEW_SIGN_IN_INFO_IN_SAMSUNG_PASS_ABB') }}</span>
        </div>
      </div>
    </div>
    <div class="app-not-ready" v-else>
      <div v-if="!isSamsungPassInstalled">
        <span>{{
          chrome.i18n
            .getMessage('PC_BR_BODY_INSTALL_PS_TO_SYNC_THE_USERNAMES_AND_PASSWORDS_FROM_YOUR_OTHER_DEVICES')
            .replace('%s', chrome.i18n.getMessage('DREAM_BR_HEADER_SAMSUNG_PASS'))
        }}</span>
        <SamsungPassRequirementVue />
      </div>
      <div v-if="isSamsungPassNotActive">
        {{
          chrome.i18n
            .getMessage('PC_BR_BODY_SIGN_IN_TO_PS_TO_SYNC_THE_USERNAMES_AND_PASSWORDS_FROM_YOUR_OTHER_DEVICES')
            .replace('%s', chrome.i18n.getMessage('DREAM_BR_HEADER_SAMSUNG_PASS'))
        }}
      </div>
      <div v-if="isSamsungPassNotSameAccount">
        <span>
          {{
            chrome.i18n.getMessage(
              'DREAM_BR_BODY_CANT_SYNC_USERNAMES_AND_PASSWORDS_BECAUSE_THE_SAMSUNG_PASS_PC_APP_AND_THE_SAMSUNG_INTERNET_EXTENSION_ARE_SIGNED_IN_TO_DIFFERENT_SAMSUNG_ACCOUNTS',
            )
          }}
        </span>
        <SamsungPassRequirementVue />
      </div>
      <div class="syncLoaderContainer" :style="{ display: isSamsungPassNotSecure ? 'block' : 'none' }">
        <div class="syncLoaderBar">
          <div class="syncLoaderValue"></div>
        </div>
      </div>
      <div class="go-to-app">
        <span class="go-install-app cursor-pointer" v-if="!isSamsungPassInstalled" @click="openSamsungPassStore">{{
          chrome.i18n.getMessage('PC_BR_BUTTON_INSTALL_PS_30').replace('%s', chrome.i18n.getMessage('DREAM_BR_HEADER_SAMSUNG_PASS'))
        }}</span>
        <span class="open-pass-and-sign-in cursor-pointer" v-if="isSamsungPassNotActive" @click="openSamsungPassApp">{{
          chrome.i18n.getMessage('PC_BR_BUTTON_SIGN_IN_TO_PS_30').replace('%s', chrome.i18n.getMessage('DREAM_BR_HEADER_SAMSUNG_PASS'))
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import appSwitchButton from '../common/app-switch.vue';
import SamsungPassRequirementVue from './samsung-pass-requirement.vue';

export default {
  name: 'CredentialHome',
  components: {
    appSwitchButton,
    SamsungPassRequirementVue,
  },
  data() {
    return {
      isSamsungPassInstalled: false,
      isSamsungPassNotActive: false,
      isSamsungPassNotSameAccount: false,
      isSamsungPassNotSecure: false,
      isSamsungPassSecured: false,
      saveSigninAndAutoSignin: true,
      saveSignin: true,
      autoSignin: true,
      visible: false,
    };
  },
  computed: {},
  watch: {},
  mounted() {
    window.CredentialHome = this;
    chrome.storage.local.get(['PASS_APP_STATUS', 'AUTOFILL_PASSWORD_ENABLED', 'SAVE_PASSWORD_ENABLED', 'ACCOUNT_NAME'], (result) => {
      if (result.PASS_APP_STATUS) {
        this.updatePassAppStatus(result.PASS_APP_STATUS);
      }
      this.autoSignin = result.AUTOFILL_PASSWORD_ENABLED != '0';
      this.saveSignin = result.SAVE_PASSWORD_ENABLED != '0';
      this.saveSigninAndAutoSignin = this.autoSignin || this.saveSignin;
      chrome.runtime.sendMessage({
        cmd: 'REQUEST_GET_PASS_APP_USER',
      });
    });
    chrome.storage.local.onChanged.addListener((changes) => {
      if (changes.PASS_APP_STATUS) {
        this.updatePassAppStatus(changes.PASS_APP_STATUS.newValue);
      }
      if (changes.AUTOFILL_PASSWORD_ENABLED) {
        this.autoSignin = this.isSamsungPassSecured && changes.AUTOFILL_PASSWORD_ENABLED.newValue != '0';
      }
      if (changes.SAVE_PASSWORD_ENABLED) {
        this.saveSignin = this.isSamsungPassSecured && changes.SAVE_PASSWORD_ENABLED.newValue != '0';
      }
      this.saveSigninAndAutoSignin = this.autoSignin || this.saveSignin;
    });
    chrome.runtime.onMessage.addListener((msg, sender, sendResponse) => {
      const { cmd, from, data } = msg;
      switch (cmd) {
        case 'VERIFY_SERVER_CERT_IN_POPUP':
          this.verifyServerCert();
          break;
      }
    });
  },
  methods: {
    open() {
      this.visible = true;
    },
    updatePassAppStatus(newValue) {
      this.isSamsungPassInstalled = newValue != PASS_APP_STATUS.NOT_INSTALLED && newValue != PASS_APP_STATUS.NOT_INSTALLED_FULL;
      this.isSamsungPassNotActive = newValue == PASS_APP_STATUS.NOT_ACTIVE;
      this.isSamsungPassNotSameAccount = newValue == PASS_APP_STATUS.NOT_SAME_USER_ID;
      this.isSamsungPassNotSecure = newValue == PASS_APP_STATUS.NOT_SECURE;
      this.isSamsungPassSecured = newValue == PASS_APP_STATUS.SECURED;
    },
    hide() {
      MenuContentView.onToggleSetting(false);
      contentTabsync.style.display = 'none';
      ContentSettings.close();
      contentBookmark.style.display = 'none';
      syncWrapper.style.display = 'block';
      settingWrapper.style.display = 'none';
      this.close();
    },
    close() {
      this.visible = false;
    },
    openSamsungPassApp() {
      window.open(`com.samsung-pass://?token=${Date.now()}`);
    },
    openSamsungPassStore() {
      window.open('ms-windows-store://pdp/?productid=9MVWDZ5KX9LH');
    },
    onChangeUsernameAndPasswordSettings() {
      if (this.saveSigninAndAutoSignin) {
        this.saveSignin = true;
        this.autoSignin = true;
        storageLocalApi.setItem('SAVE_PASSWORD_ENABLED', '1');
        storageLocalApi.setItem('AUTOFILL_PASSWORD_ENABLED', '1');
      } else {
        this.saveSignin = false;
        this.autoSignin = false;
        storageLocalApi.setItem('SAVE_PASSWORD_ENABLED', '0');
        storageLocalApi.setItem('AUTOFILL_PASSWORD_ENABLED', '0');
      }
      chrome.runtime.sendMessage({
        cmd: 'UPDATE_SAVE_PASSWORD_ENABLED',
      });
      chrome.runtime.sendMessage({
        cmd: 'UPDATE_AUTOFILL_PASSWORD_ENABLED',
      });
      chrome.runtime.sendMessage({
        cmd: 'REQUEST_GET_PASS_APP_USER',
      });
    },
    onChangeSaveSigninSettings() {
      storageLocalApi.setItem('SAVE_PASSWORD_ENABLED', this.saveSignin ? '1' : '0');
      chrome.runtime.sendMessage({
        cmd: 'UPDATE_SAVE_PASSWORD_ENABLED',
      });
      chrome.runtime.sendMessage({
        cmd: 'REQUEST_GET_PASS_APP_USER',
      });
      this.saveSigninAndAutoSignin = this.saveSignin || this.autoSignin;
    },
    onChangeAutoSigninSettings() {
      storageLocalApi.setItem('AUTOFILL_PASSWORD_ENABLED', this.autoSignin ? '1' : '0');
      chrome.runtime.sendMessage({
        cmd: 'UPDATE_AUTOFILL_PASSWORD_ENABLED',
      });
      chrome.runtime.sendMessage({
        cmd: 'REQUEST_GET_PASS_APP_USER',
      });
      this.saveSigninAndAutoSignin = this.saveSignin || this.autoSignin;
    },
    verifyServerCert() {
      chrome.storage.local.get(['SERVER_CERT'], function (result) {
        const data = result.SERVER_CERT;
        const pki = forge.pki;
        const rootCert = pki.certificateFromPem(data.rootCert);
        const deviceCert = pki.certificateFromAsn1(forge.asn1.fromDer(forge.util.decode64(data.deviceCert)));
        const serverCert = pki.certificateFromAsn1(forge.asn1.fromDer(forge.util.decode64(data.serverCert)));
        if (rootCert.verify(serverCert) && serverCert.verify(deviceCert)) {
          const devicePublicKeyPem = forge.pki.publicKeyToPem(deviceCert.publicKey);
          chrome.storage.local.set(
            {
              SPASS_DEVICE_PEM_PUBLIC_KEY: devicePublicKeyPem,
            },
            () => {
              chrome.runtime.sendMessage({
                cmd: 'VERIFY_SERVER_CERT_SUCCESS',
                data: {
                  devicePublicKey: deviceCert.publicKey,
                  devicePublicKeyPem: devicePublicKeyPem,
                },
              });
            },
          );
        }
      });
    },
  },
};
</script>
<style scoped src="./CredentialHome.css"></style>
