<template>
  <AppModal id="add-modal-container" v-if="visible">
    <template slot="header">{{ chrome.i18n.getMessage('ADD_TO_BOOKMARKS_TEXT') }}</template>
    <div class="app-modal-content" @keyup.esc="toggle">
      <div class="form-control" :class="{ error }">
        <label class="name-text" name="bookmark-name">{{ chrome.i18n.getMessage('NAME') }}</label>
        <input id="addTitleInput" class="add-bookmark-title" type="text" v-model.trim="bookmark.title" @focus="focusInput" @click="focusInput" @keyup="focusInput" tabindex="1" />
        <div class="error-text" v-if="error">
          <span>{{ error }}</span>
        </div>
      </div>
      <div class="form-control">
        <label class="folder-text" name="folder-name">{{ chrome.i18n.getMessage('FOLDER') }}</label>
        <app-input :options="recentFolders" @select="chooseFolder" />
      </div>
    </div>
    <div class="app-modal-controls">
      <button class="modal-control edit-text" @click="openEdit" tabindex="4">{{ chrome.i18n.getMessage('CONTEXT_EDIT') }}</button>
      <button class="modal-control primary ok-text" @click="confirm" tabindex="5">{{ chrome.i18n.getMessage('OK') }}</button>
      <button class="modal-control modal-button-cancel" @click="toggle" tabindex="6">{{ chrome.i18n.getMessage('CANCEL') }}</button>
    </div>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';

export default {
  name: 'AddModal',
  components: {
    AppModal,
  },
  data() {
    return {
      bookmark: {
        title: '',
        url: '',
        parent: 0,
        isFolder: false,
      },
      visible: false,
      folders: [],
      error: '',
      firstInput: true,
    };
  },
  mounted() {
    window.AddModal = this;
  },
  updated() {
    if (this.visible && this.firstInput && this.bookmark.title) {
      document.getElementById('addTitleInput').select();
      this.firstInput = false;
    }
  },
  methods: {
    toggle() {
      this.visible = !this.visible;
      this.folders = [];
    },
    async confirm() {
      this.bookmark.title = this.bookmark.title.trim();
      if (!this.bookmark.title) {
        this.error = TITLE_EMPTY;
        return;
      }

      if (isBrowserURL(this.bookmark.url)) {
        this.error = URL_INVALID;
        return;
      }

      const doesSimilarExist = await checkSimilarBookmark(this.bookmark);

      if (doesSimilarExist) {
        this.error = BOOKMARK_EXISTS;
        return;
      }

      await addBookmark(this.bookmark);
      addNodeNewBookmark(this.bookmark);
      doBookmarkSync();
      this.toggle();
    },
    openEdit() {
      EditModal.bookmark.parent = this.bookmark.parent;
      EditModal.editedTitle = this.bookmark.title.trim();
      EditModal.toggle();

      this.toggle();
    },
    chooseFolder(option) {
      if (this.visible) {
        this.error = '';
      }
      if (option.value === 'other') {
        this.openEdit();
      } else {
        this.bookmark.parent = option.value;
      }
    },
    focusInput() {
      if (this.error) {
        this.error = '';
      }
    },
  },
  computed: {
    recentFolders() {
      return this.folders.slice(0, 5).concat({ title: CHOOSE_OTHER_FOLDER, value: 'other', class: 'chooseFolder' });
    },
  },
  watch: {
    async visible(isVisible) {
      storageLocalApi.setItem(IS_MODAL_OPENED, isVisible ? 'true' : 'false');
      if (isVisible) {
        const { title, url } = await getActiveTab();

        this.bookmark.title = title;
        this.bookmark.url = url;

        const folderResult = Array.from(
          await DB.Query.bookmarks({
            selection: ['_ID as value', 'TITLE as title'],
            where: {
              equals: [
                { key: 'IS_DELETED', value: '0' },
                { key: 'FOLDER', value: '1' },
              ],
            },
            sortOrder: 'MODIFIED DESC',
          }),
        );

        folderResult.forEach((n) => {
          this.folders.push({ title: n.title, value: n.value, class: 'option' });
        });

        // Set the default option based on recently selected folder
        let primaryFolder = ROOT_FOLDER;

        if (CURRENT_FOLDER_ID) {
          primaryFolder = folderResult.find((folder) => folder.value === CURRENT_FOLDER_ID);

          primaryFolder.class = 'option';
          const idx = this.folders.findIndex(function (item) {
            return item.value === CURRENT_FOLDER_ID;
          });
          // if the primaryFolder is exist, remove it in folder list (prevent to display duplicated)
          if (idx > -1) {
            this.folders.splice(idx, 1);
          }
        }

        this.bookmark.parent = primaryFolder.value;
        this.folders = [primaryFolder].concat(this.folders);

        const doesSimilarExist = await checkSimilarBookmark(this.bookmark);

        if (doesSimilarExist) {
          this.error = BOOKMARK_EXISTS;
        }

        if (isBrowserURL(this.bookmark.url)) {
          this.error = URL_INVALID;
        }
      } else {
        this.error = '';
        this.firstInput = true;
      }
    },
  },
};
</script>
