<template>
  <div class="app-switch-container">
    <div class="appSwitch">
      <label>
        <span class="switch-text" :class="{ isOn: value }" v-if="isLabel">{{ value ? onLabel : offLabel }}</span>
        <img class="switch-icon" src="/assets/img/Switch_on.svg" v-if="value" @click="onChange(false)" />
        <img class="switch-icon" src="/assets/img/Switch_off.svg" v-else @click="onChange(true)" />
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'appSwitchButton',
  props: {
    value: {
      type: Boolean,
    },
    isLabel: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      onLabel: chrome.i18n.getMessage('DREAM_ST_SBODY_ON'),
      offLabel: chrome.i18n.getMessage('DREAM_ST_SBODY_OFF'),
    };
  },
  mounted() {
    window.appSwitch = this;
  },
  methods: {
    onChange(value) {
      this.$emit('input', value);
      this.$emit('onChange', value);
    },
  },
};
</script>
