<template>
  <div class="app-radio-container">
    <div class="radio-option" v-for="option in options" :key="option.value" @click="select(option)">
      <img class="radio-icon" src="/assets/img/internet_pc_ic_Radio_on.svg" v-if="option.value === value" />
      <img class="radio-icon" src="/assets/img/internet_pc_ic_Radio_off.svg" v-else />
      <span class="app-radio-title">{{ option.title }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'appRadioGroup',
  props: ['options', 'value'],
  methods: {
    select(option) {
      this.$emit('input', option.value);
    },
  },
};
</script>
