<template>
  <AppModal id="import-modal-container" v-if="visible">
    <template slot="header">{{ chrome.i18n.getMessage('IMPORT_TEXT', browserName) }}</template>
    <div class="app-modal-content">
      <!-- <span class="import-description import-help-text"></span> -->
      <!-- <div class="do-not-show-container">
        <span class="do-not-show-title" @click="toggleCheckBox"></span>
        <app-checkbox v-model="doNotShow" tabindex="0"/>
      </div> -->
      <div class="modal-button-area">
        <div class="modal-import-button-area">
          <div tabindex="1" class="import-popup-button" @click="onSelectType('add')">
            <img class="radio-icon" src="/assets/img/internet_pc_ic_Radio_on.svg" v-if="selectedType === 'add'" />
            <img class="radio-icon" src="/assets/img/internet_pc_ic_Radio_off.svg" v-else />
            <div class="impot-button-text">
              <div class="import-button-title modal-button-overwrite">{{ chrome.i18n.getMessage('DREAM_BR_OPT_ADD_TO_CURRENT_PS_BOOKMARKS').replace('%s', browserName) }}</div>
              <!-- <div class="import-button-desc modal-button-overwrite-desc"></div> -->
            </div>
          </div>
          <div tabindex="2" class="import-popup-button" @click="onSelectType('replace')">
            <img class="radio-icon" src="/assets/img/internet_pc_ic_Radio_on.svg" v-if="selectedType === 'replace'" />
            <img class="radio-icon" src="/assets/img/internet_pc_ic_Radio_off.svg" v-else />
            <div class="impot-button-text">
              <div class="import-button-title modal-button-replace">{{ chrome.i18n.getMessage('DREAM_BR_OPT_REPLACE_CURRENT_PS_BOOKMARKS').replace('%s', browserName) }}</div>
              <!-- <div class="import-button-desc modal-button-replace-desc"></div> -->
            </div>
          </div>
          <button tabindex="-1" id="import-popup-cancel" class="modal-button-cancel">{{ chrome.i18n.getMessage('CANCEL') }}</button>
        </div>
      </div>
    </div>
    <div class="app-modal-controls modal-footer">
      <button :disabled="!selectedType" class="primary modal-button-import" @click="selectType(selectedType)" @keypress.enter="selectType(selectedType)">
        {{ chrome.i18n.getMessage('IMPORT') }}
      </button>
      <button class="modal-button-cancel" @click="toggle">{{ chrome.i18n.getMessage('CANCEL') }}</button>
    </div>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';

export default {
  name: 'ImportChromeModal',
  components: {
    AppModal,
  },
  data() {
    return {
      browserName: [],
      visible: false,
      doNotShow: false,
      selectedType: '',
    };
  },
  mounted() {
    window.ImportModal = this;
    this.browserName = getBrowserName();
  },
  methods: {
    toggleCheckBox() {
      EventBus.$emit('checkboxToggle');
    },
    toggle() {
      this.selectedType = '';
      this.visible = !this.visible;
    },
    onSelectType(type) {
      this.selectedType = type;
    },
    selectType(type) {
      this.toggle();
      switch (type) {
        case 'replace':
          if (this.doNotShow) {
            storageLocalApi.setItem(IMPORT_OPTION, '2');
          }
          ImportNotifyModal.toggle();
          replaceImported()
            .then((res) => {
              const { err, length } = res;
              if (length > 0) {
                ImportNotifyModal.importDone(length);
              } else {
                ImportNotifyModal.nothingToImport();
              }
            })
            .then(doBookmarkSync);
          break;
        case 'add':
          if (this.doNotShow) {
            storageLocalApi.setItem(IMPORT_OPTION, '3');
          }
          ImportNotifyModal.toggle();
          addToImported()
            .then((res) => {
              const { err, length } = res;
              if (length > 0) {
                ImportNotifyModal.importDone(length);
              } else {
                ImportNotifyModal.nothingToImport();
              }
            })
            .then(doBookmarkSync);
          break;
      }

      // Update 'Do not show'
      storageLocalApi.setItem(IMPORT_POPUP_CHECK, this.doNotShow);
    },
  },
};
</script>
