<template>
  <AppModal id="signout-modal-container" v-if="visible">
    <template slot="header">{{ modalTitle }}</template>
    <div class="app-modal-content" @keyup.esc="toggle">
      <span class="signout-title">{{ chrome.i18n.getMessage('IDS_ST_SK3_SIGN_OUT') }}</span>
      <div class="signout-notice">
        <span>{{ chrome.i18n.getMessage('PC_SA_BODY_YOUR_SAMSUNG_ACCOUNT_WILL_BE_REMOVED_FROM_THIS_COMPUTER') }}</span>
      </div>
      <div class="signout-notice">
        <div class="samsung-cloud-img">
          <img src="/assets/img/samsung_cloud.svg" />
        </div>
        <div class="notice-content">
          <span>{{ chrome.i18n.getMessage('PC_SA_HEADER_APPS_WILL_STOP_SYNCING') }}</span>
          <span>{{ chrome.i18n.getMessage('PC_SA_BODY_ALL_SAMSUNG_APPS_ON_THIS_COMPUTER_WILL_STOP_SYNCING') }}</span>
        </div>
      </div>
    </div>
    <div class="app-modal-controls signout-buttons">
      <button id="logOut" class="primary signout-modal" @click="confirm">{{ chrome.i18n.getMessage('IDS_ST_SK3_SIGN_OUT') }}</button>
      <button class="signout-modal modal-button-cancel" @click="toggle">{{ chrome.i18n.getMessage('CANCEL') }}</button>
    </div>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';

export default {
  name: 'LogoutModal',
  components: {
    AppModal,
  },
  data() {
    return {
      modalTitle: '',
      visible: false,
      texts: {},
    };
  },
  mounted() {
    window.SignoutModal = this;
  },
  methods: {
    onInit() {
      this.texts = {
        SIGN_OUT,
        SIGN_OUT_TITLE,
        SIGN_OUT_DES_1,
        SIGN_OUT_DES_2,
      };
    },
    toggle() {
      this.visible = !this.visible;
    },
    confirm() {
      onLogoutClick();
      this.toggle();
    },
  },
  watch: {
    async visible(isVisible) {
      storageLocalApi.setItem(IS_MODAL_OPENED, isVisible ? 'true' : 'false');
    },
  },
};
</script>
