<template>
  <AppModal id="edit-modal-container" v-if="visible">
    <template slot="header">{{ modalTitle }}</template>
    <div class="app-modal-content" @keyup.esc="toggle">
      <div class="form-control" :class="{ error: error.title }">
        <label class="name-text" for="folder-name">{{ chrome.i18n.getMessage('NAME') }}</label>
        <input
          id="editTitleInput"
          type="text"
          name="bookmark-name"
          v-model.trim="bookmark.title"
          @focus="focusInputForTitle"
          @click="focusInputForTitle"
          @keyup="focusInputForTitle"
        />
        <div class="error-text" v-if="error.title">
          <span>{{ error.title }}</span>
        </div>
      </div>
      <div class="form-control" :class="{ error: error.url }" v-if="!bookmark.isFolder">
        <label class="url-text" for="folder-name">{{ chrome.i18n.getMessage('WEB_ADDRESS') }}</label>
        <input type="text" name="bookmark-url" v-model.trim="bookmark.url" @focus="focusInputForUrl" />
        <div class="error-text" v-if="error.url">
          <span>{{ error.url }}</span>
        </div>
      </div>
      <div class="location">
        <span class="location-text">{{ chrome.i18n.getMessage('DREAM_BR_HEADER_LOCATION') }}</span>
        <div class="bookmarks-tree">
          <tree :tree="tree" :selected="bookmark.parent" :target="bookmark" />
        </div>
      </div>
    </div>
    <div class="create-folder" :class="{ disabled: disabled }" @click="createFolder" @keypress.enter="createFolder">
      <img src="/assets/img/internet_pc_ic_popup_add.svg" />
      <span class="create-folder-text">{{ chrome.i18n.getMessage('DREAM_BR_BUTTON_CREATE_FOLDER_IN_SELECTED_FOLDER_32') }}</span>
    </div>
    <div class="app-modal-controls edit-modal">
      <button id="edit-modal-primary" class="primary modal-button-save" @click="confirm">{{ chrome.i18n.getMessage('DREAM_BR_BUTTON_SAVE_M_PASSWORD_25') }}</button>
      <button class="modal-button-cancel" @click="toggle">{{ chrome.i18n.getMessage('CANCEL') }}</button>
    </div>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';
import tree from '../common/bookmark-tree.vue';

export default {
  name: 'EditModal',
  components: {
    AppModal,
    tree,
  },
  data() {
    return {
      modalTitle: '',
      bookmark: {
        id: '',
        title: '',
        url: '',
        parent: '0',
        isFolder: false,
      },
      existingBookmark: false,
      visible: false,
      editedTitle: '',
      tree: {},
      error: {
        title: '',
        url: '',
      },
      shouldSync: false,
      firstInput: true,
      isShowNewFolder: false,
      isConfirmClicked: false,
      targetNode: {},
      disabled: false,
    };
  },
  mounted() {
    window.EditModal = this;
    this.onInit();
  },
  updated() {
    if (this.firstInput && this.bookmark.title) {
      document.getElementById('editTitleInput').select();
      this.firstInput = false;
    }
  },
  methods: {
    onInit() {
      this.modalTitle = chrome.i18n.getMessage('EDIT_BOOKMARK_TEXT');
      EventBus.$on('firstSelectLeaf', (leaf) => {
        this.disabled = this.targetNode.data && leaf == this.targetNode.data.id;
        if (!this.bookmark.isFolder) {
          this.bookmark.parent = leaf;
        }
      });
      EventBus.$on('selectLeaf', (leaf) => {
        this.error.title = '';
        this.error.url = '';
        this.bookmark.parent = leaf;
        this.disabled = this.targetNode.data && leaf == this.targetNode.data.id;
      });
      EventBus.$on('isShowNewFolder', (isShowNewFolder) => {
        this.isShowNewFolder = isShowNewFolder;
        if (!isShowNewFolder && this.isConfirmClicked) {
          this.isConfirmClicked = false;
          this.confirm();
        }
      });
      EventBus.$on('newFolderInfo', (newFolderInfo) => {
        if (this.visible) {
          this.shouldSync = true;
          var parentNode = getNodeById(parseInt(newFolderInfo.parent));
          var node = addNewNode(parentNode, newFolderInfo);
          EditModal.bookmark.key = node.key;
        }
      });
    },
    toggle() {
      this.visible = !this.visible;
      if (!this.visible) {
        this.firstInput = true;
      }
    },
    async confirm() {
      if (this.isShowNewFolder) {
        this.isConfirmClicked = true;
        return;
      }

      let hasErrors = false;

      if (!this.bookmark.title) {
        this.error.title = TITLE_EMPTY;
        hasErrors = true;
      }

      if (!this.bookmark.isFolder) {
        if (!this.bookmark.url) {
          this.error.url = URL_EMPTY;
          hasErrors = true;
        }

        if (isBrowserURL(this.bookmark.url)) {
          this.error.url = URL_INVALID;
          hasErrors = true;
        }
      }

      const doesSimilarExist = await checkSimilarBookmark(this.bookmark);

      if (doesSimilarExist && this.existingBookmark) {
        var options = {
          body: BOOKMARK_EXISTS,
          icon: 'assets/img/logo48x48.png',
        };
        const not = new Notification('', options);
        setTimeout(not.close.bind(not), 3000);
        return this.toggle();
      }

      if (doesSimilarExist) {
        this.error.title = BOOKMARK_EXISTS;
        hasErrors = true;
      }

      if (hasErrors) {
        return;
      }

      if (this.existingBookmark) {
        await updateBookmark(this.bookmark);
        updateBookmarkCallback(this.targetNode, this.bookmark);
      } else if (this.bookmark.isFolder) {
        await createFolder(this.bookmark);
      } else {
        await addBookmark(this.bookmark);
        addNodeNewBookmark(this.bookmark);
      }

      doBookmarkSync();
      this.toggle();
    },
    createFolder() {
      EventBus.$emit('showNewFolder', this.bookmark.parent);
    },
    async updateFolderTree() {
      this.tree = {
        children: [await getDBBookmarkTree()],
      };
    },
    focusInputForTitle() {
      if (this.error.title) {
        this.error.title = '';
      }
    },
    focusInputForUrl() {
      if (this.error.url) {
        this.error.url = '';
      }
    },
  },
  watch: {
    async visible(isVisible) {
      storageLocalApi.setItem(IS_MODAL_OPENED, isVisible ? 'true' : 'false');
      this.tree = {};
      if (!isVisible) {
        this.bookmark = {
          title: '',
          url: '',
          isFolder: false,
          parent: 0,
        };
        this.isShowNewFolder = false;
        this.error = { title: '', url: '' };
        this.existingBookmark = false;

        if (this.shouldSync) {
          doBookmarkSync();
          this.shouldSync = false;
        }

        return;
      }

      this.modalTitle = this.bookmark.isFolder ? EDIT_FOLDER_TEXT : EDIT_BOOKMARK_TEXT;

      // BUG: Editing existing folders, allows to move into itself
      await this.updateFolderTree();

      // Check if bookmark already set
      if (this.bookmark.title) {
        this.existingBookmark = true;
        return;
      }

      this.modalTitle = ADD_TO_BOOKMARKS_TEXT;
      // Get Bookmark details from active tab
      const { title, url } = await getActiveTab();

      this.bookmark.title = this.editedTitle == '' ? title : this.editedTitle;
      this.bookmark.url = url;

      const doesSimilarExist = await checkSimilarBookmark(this.bookmark);
      if (doesSimilarExist) {
        this.error.title = BOOKMARK_EXISTS;
      }

      if (isBrowserURL(this.bookmark.url)) {
        this.error.url = URL_INVALID;
      }
    },
  },
};
</script>
