<template>
  <AppModal id="move-modal-container" v-if="visible">
    <template slot="header">{{ chrome.i18n.getMessage('DREAM_BR_HEADER_SELECT_LOCATION_M_FILE_LOCATION') }}</template>
    <div class="app-modal-content" @keyup.esc="toggle">
      <div class="error-text" v-if="errorMessage">
        <span>{{ errorMessage }}</span>
      </div>
      <div class="bookmarks-tree">
        <tree :tree="tree" :selected="parent.id" :target="selectedBookmarks" />
      </div>
    </div>
    <div class="create-folder" @click="createFolder" @keypress.enter="createFolder">
      <img src="/assets/img/internet_pc_ic_popup_add.svg" />
      <span class="create-folder-text">{{ chrome.i18n.getMessage('DREAM_BR_BUTTON_CREATE_FOLDER_IN_SELECTED_FOLDER_32') }}</span>
    </div>
    <div class="app-modal-controls edit-modal">
      <!-- <button id="new-folder-control" class="modal-control new-folder-text" @click="createFolder" @keypress.enter="createFolder"></button> -->
      <button id="edit-modal-primary" class="primary modal-button-save" @click="confirm">{{ chrome.i18n.getMessage('DREAM_BR_BUTTON_SAVE_M_PASSWORD_25') }}</button>
      <button class="modal-button-cancel" @click="toggle">{{ chrome.i18n.getMessage('CANCEL') }}</button>
    </div>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';
import tree from '../common/bookmark-tree.vue';

export default {
  name: 'MoveBookmarkModal',
  components: {
    AppModal,
    tree,
  },
  data() {
    return {
      modalTitle: '',
      parent: {
        id: '0',
        key: '',
      },
      selectedBookmarks: [],
      newFolderKey: '',
      visible: false,
      tree: {},
      errorMessage: '',
      shouldSync: false,
      isShowNewFolder: false,
      isConfirmClicked: false,
      targetNode: {},
    };
  },
  mounted() {
    window.MoveModal = this;
    EventBus.$on('firstSelectLeaf', (leaf) => {
      this.parent.id = leaf;
    });
    EventBus.$on('selectLeaf', (leaf) => {
      this.errorMessage = '';
      this.parent.id = leaf;
    });
    EventBus.$on('isShowNewFolder', (isShowNewFolder) => {
      this.isShowNewFolder = isShowNewFolder;
      if (!isShowNewFolder && this.isConfirmClicked) {
        this.isConfirmClicked = false;
        this.confirm();
      }
    });
    EventBus.$on('newFolderInfo', (newFolderInfo) => {
      if (this.visible) {
        var parentNode = getNodeById(parseInt(newFolderInfo.parent));
        var node = addNewNode(parentNode, newFolderInfo);
        this.parent.key = node.key;
      }
    });
  },
  methods: {
    toggle() {
      this.visible = !this.visible;
    },
    async confirm() {
      if (this.isShowNewFolder) {
        this.isConfirmClicked = true;
        return;
      }
      const bookmarks = [];
      const checkSimilarPromises = Array.from(this.selectedBookmarks).map((item) => {
        const bookmark = {
          id: item.data.id,
          parent: this.parent.id,
          title: item.title,
          url: item.data.url,
          isFolder: item.folder,
        };
        bookmarks.push(bookmark);
        return checkSimilarBookmark(bookmark);
      });
      const doesSimilarExist = await Promise.all(checkSimilarPromises);
      if (doesSimilarExist.some((value) => value)) {
        this.errorMessage = BOOKMARK_EXISTS;
        return;
      }

      moveBookmarksCallback(this.parent, this.selectedBookmarks);
      const updateMoveBookmarks = Array.from(bookmarks).map((bookmark) => {
        return updateBookmark(bookmark);
      });
      await Promise.all(updateMoveBookmarks);

      doBookmarkSync();
      this.toggle();
    },
    createFolder() {
      EventBus.$emit('showNewFolder', this.parent.id);
      this.shouldSync = true;
    },
    async updateFolderTree() {
      this.tree = {
        children: [await getDBBookmarkTree()],
      };
    },
  },
  watch: {
    async visible(isVisible) {
      storageLocalApi.setItem(IS_MODAL_OPENED, isVisible ? 'true' : 'false');
      if (!isVisible) {
        this.errorMessage = '';
        this.existingBookmark = false;

        if (this.shouldSync) {
          doBookmarkSync();
          this.shouldSync = false;
        }

        return;
      }

      // BUG: Editing existing folders, allows to move into itself
      await this.updateFolderTree();
    },
  },
};
</script>
