<template>
  <AppModal id="import-notify-modal-container" v-if="visible">
    <template slot="header">{{ chrome.i18n.getMessage('IMPORT') }}</template>
    <span class="import-notify-description msg">{{ modalNotify }}</span>
  </AppModal>
</template>

<script>
import AppModal from '../common/app-modal.vue';

export default {
  name: 'ImportNotify',
  components: {
    AppModal,
  },
  data() {
    return {
      modalTitle: chrome.i18n.getMessage('IMPORT'),
      modalNotify: chrome.i18n.getMessage('IMPORTING'),
      visible: false,
    };
  },
  mounted() {
    window.ImportNotifyModal = this;
  },
  methods: {
    toggle() {
      this.visible = !this.visible;
      this.modalNotify = IMPORTING;
    },
    importDone(len) {
      this.modalNotify = `${IMPORTED} ${len} ${ITEMS_TEXT}!`;
      setTimeout(() => {
        this.toggle();
      }, 1500);
    },
    nothingToImport() {
      this.modalNotify = ALREADY_IMPORTED;
      setTimeout(() => {
        this.toggle();
      }, 1500);
    },
  },
  updated() {
    if (this.visible) {
      document.querySelectorAll('.modal-close').forEach((item) => {
        item.style.display = 'none';
      });
    }
  },
};
</script>
