</template>
<script>
import ContentSettings from './popup/Components/ContentSettings/ContentSettings.vue';
import AddModal from './popup/Components/modals/add-modal.vue';
import CreateModal from './popup/Components/modals/create-modal.vue';
import EditModal from './popup/Components/modals/edit-modal.vue';
import ImportChromeModal from './popup/Components/modals/import-modal.vue';
import ImportNotifyModal from './popup/Components/modals/import-notify-modal.vue';
import LogoutModal from './popup/Components/modals/signout-modal.vue';
import MoveBookmarkModal from './popup/Components/modals/move-modal.vue';
import CredentialHome from './popup/Components/passwords/CredentialHome.vue';

export default {
  name: 'App',
  components: {
    ContentSettings,
    AddModal,
    CreateModal,
    EditModal,
    ImportChromeModal,
    ImportNotifyModal,
    LogoutModal,
    MoveBookmarkModal,
    CredentialHome,
  },
  data() {
    return {
      element: '',
      scriptFiles: [
        'js/popup/strings.js',
        'js/utils/commonUtils.js',

        // Database -->
        'js/sync/Database/Indexeddb.js',
        'js/sync/Database/ContentProvider.js',
        'js/sync/Database/BatchOperation.js',

        // List views -->
        'js/popup/utils/favicon.js',

        // Util Functions -->
        'js/utils/util.js',
        'js/utils/dbUtils.js',
        'js/utils/bookmarkUtils.js',

        // Edit/Delete button click handlers -->
        'js/popup/bookmark-edit-remove-handlers.js',
        'js/popup/tab-edit-remove-handlers.js',

        // Message Listeners from localserver and js/sync -->
        'js/popup/messageListener.js',

        // Modal Actions -->
        'js/popup/utils/focusedWindowListener.js',
        'js/popup/utils/importUtils.js',

        // Sync Page Rendering -->
        'js/popup/sync-page.js',

        // Login Page Rendering, TODO: Move stuff to util/ui-init etc -->
        'js/popup/login-page.js',

        // Context Menu -->
        'js/context-menus/util.js',
        'js/context-menus/browserAction.js',

        // Initialization -->
        'js/popup/index.js',

        // Fancy Tree Scripts -->
        'js/popup/sync-content-tree.js',
        'js/popup/search-page.js',
        'js/popup/bookmark-tree-handlers.js',
      ],
      importing: false,
      viewFolder: {},
      stackNodes: [],
      onSetting: false,
      isSyncing: false,
      isSearchEnabled: false,
      searchResultsEnabled: false,
      searchInput: '',
      onEditSearchResults: false,
      isOpenEditBar: false,
      selectedBookmarkCnt: 0,
      bookmarksViewCnt: 0,
      searchResults: {
        SBROWSER: [],
        openTabs: [],
        CHROME: [],
        EDGE: [],
      },
      texts: {
        ITEMS_SELECTED: chrome.i18n.getMessage('PLURAL_OTHER_ST_HEADER_PD_SELECTED'),
        IMPORT_CHROME_BOOKMARKS: chrome.i18n.getMessage('DREAM_BR_HEADER_IMPORT_PS_BOOKMARKS').replace('%s', getBrowserName()),
        SYNCING: chrome.i18n.getMessage('DREAM_CLOUD_BODY_SYNCING_ING'),
        EDIT: chrome.i18n.getMessage('CONTEXT_EDIT'),
        DELETE: chrome.i18n.getMessage('DREAM_MSG_OPT_DELETE'),
        DELETE_ALL: chrome.i18n.getMessage('IDS_BR_OPT_BROWSERSAVEDPAGESDELALL'),
        SELECT_ALL: chrome.i18n.getMessage('PC_ST_BUTTON_SELECT_ALL_20'),
        DESELECT_ALL: chrome.i18n.getMessage('PC_ST_BUTTON_DESELECT_ALL_20'),
        NO_SEARCH_RESULT: chrome.i18n.getMessage('NO_SEARCH_RESULT'),
        UNDO: chrome.i18n.getMessage('IDS_ST_BUTTON_UNDO'),
      },
      notification: {
        message: '',
        action: '',
        type: '',
      },
      importBadge: '',
      chromeBookmarkBadge: '',
      currentBrowserId: IMPORTED_FOLDER_IDS[getBrowserName().toUpperCase()],
      isShowSettingsBadge: false,
      isShowUsernamePasswordBadge: false,
    };
  },
  computed: {
    backIconEnabled() {
      return this.viewFolder.data && Object.keys(IMPORTED_FOLDER_IDS).filter((key) => IMPORTED_FOLDER_IDS[key] == this.viewFolder.data.id).length == 0;
    },
  },
  watch: {
    isOpenEditBar(visible) {
      if (visible) {
        this.onChangeSelectedNodes();
      } else {
        mainTree.visit(function (node) {
          node.setSelected(false);
          node.setActive(false);
        });
      }
    },
    onEditSearchResults(isOpen) {
      Object.keys(resultsTree).forEach((key) => {
        refreshTreeSelectStatus(resultsTree[key]);
      });
      if (isOpen) {
        this.onChangeSelectedSearchItems();
      }
    },
    isSearchEnabled(isOpen) {
      if (!isOpen) {
        this.onChangeSelectedNodes();
      }
    },
  },
  mounted() {
    window.MenuContentView = this;
    this.scriptFiles.forEach((fileUrl) => {
      this.element = document.createElement('script');
      this.element.type = 'text/javascript';
      this.element.async = false;
      this.element.src = fileUrl;
      document.body.appendChild(this.element);
    });
    this.isSyncing = true;
    storageLocalApi.waitForAvailable().then(() => {
      this.importBadge = storageLocalApi.getItem(IMPORT_AVAILABLE) ? '/assets/img/internet_pc_ic_badge_new.svg' : '';
      this.chromeBookmarkBadge = storageLocalApi.getItem(IMPORT_AVAILABLE) ? '/assets/img/internet_pc_ic_badge_new.svg' : '';
      this.isShowUsernamePasswordBadge = storageLocalApi.getItem('PASSWORD_SYNC_BADGE_SHOWED_1ST') ? false : true;
    });
    this.noticeRemovePassSync();
  },
  methods: {
    dialogAddBookmark(e) {
      if (e.currentTarget.classList.contains('syncing')) return;
      window.EditModal.bookmark.parent = originNode.data.id;
      window.EditModal.toggle();
      document.dispatchEvent(
        new CustomEvent('analytics', {
          detail: {
            action: 'FLOATING_ICON-ADD',
            event: e,
          },
        }),
      );
    },
    importButtonClick(e) {
      this.importing = true;
      e.stopPropagation();
      if (e.currentTarget.classList.contains('syncing')) return;
      document.dispatchEvent(
        new CustomEvent('analytics', {
          detail: {
            action: 'IMPORT_BUTTON',
            event: e,
          },
        }),
      );

      if (storageLocalApi.getItem(IMPORT_OPTION) != '1' || storageLocalApi.getItem(IMPORT_POPUP_CHECK) === true) {
        triggerImport();
      } else {
        ImportModal.toggle();
      }
      switchImportButton(false);
    },
    goBack() {
      originNode = getNodeById(originNode.data.id).parent;
      viewNode(originNode);
      this.stackNodes.pop();
    },
    onClickSearch() {
      this.isSearchEnabled = true;
      this.$nextTick(() => {
        this.$refs.search.focus({
          preventScroll: true,
        });
      });
    },
    onToggleSetting(mode) {
      this.onSetting = mode;
      document.getElementById('side-header').classList[mode ? 'add' : 'remove']('settingOn');
      document.getElementById('side-header').classList.remove('usernamePasswordOn');
      this.isShowSettingsBadge = false;
    },
    onToggleUsernamePassword(mode) {
      this.onSetting = mode;
      document.getElementById('side-header').classList[mode ? 'add' : 'remove']('usernamePasswordOn');
      document.getElementById('side-header').classList.remove('settingOn');
      this.isShowSettingsBadge = false;
    },
    updateStackNodes(node) {
      if (this.stackNodes.length > 0) {
        this.stackNodes.pop();
        this.stackNodes.push(node);
      }
    },
    closeSearching() {
      this.isSearchEnabled = false;
      this.searchResultsEnabled = false;
      this.searchInput = '';
      this.toggleEditSearchMode(false);
      this.resetSearchTab();
    },
    resetSearchTab() {
      var source = {
        searchResultTree: {
          children: [],
        },
        chromeResultTree: {
          children: [],
        },
        openTabsResultTree: {
          children: [],
        },
        edgeResultTree: {
          children: [],
        },
      };
      searchResultTreeInit(source, this.searchInput);
    },
    triggerSearch(e) {
      this.searchInput = e && e.target ? e.target.value : this.searchInput;
      var node = folderTree.getRootNode();
      var target = '';
      Object.keys(this.searchResults).forEach((key) => {
        this.searchResults[key] = [];
      });
      const _recurse = (node) => {
        if (node.data.id != undefined && node.data.id != IMPORTED_MAIN_FOLDER_IDS.BOOKMARKS && node.data.id != IMPORTED_MAIN_FOLDER_IDS.TABS) {
          if (node.data.id != IMPORTED_FOLDER_IDS.CHROME && node.data.id != IMPORTED_FOLDER_IDS.SBROWSER && node.data.id != IMPORTED_FOLDER_IDS.EDGE) {
            if (
              node.title.toLowerCase().indexOf(this.searchInput.toLowerCase()) != -1 ||
              (node.data.url && node.data.url.toLowerCase().indexOf(this.searchInput.toLowerCase()) != -1)
            ) {
              this.searchResults[target].push(node);
            }
          } else {
            target = IMPORTED_FOLDER_NAMES[node.data.id];
          }
        }
        const children = node.children;
        if (children && children.length > 0) {
          children.forEach((c) => _recurse(c));
        }
      };
      if (this.searchInput) {
        _recurse(node);
      }
      var source = {
        searchResultTree: {
          children: this.searchResults.SBROWSER,
        },
        chromeResultTree: {
          children: this.searchResults.CHROME,
        },
        openTabsResultTree: {
          children: this.openTabs,
        },
        edgeResultTree: {
          children: this.searchResults.EDGE,
        },
      };
      searchResultTreeInit(source, this.searchInput);
      this.getBookmarkCounts();
      this.searchResultsEnabled = true;
    },
    onDblClickSearchResult(data) {
      if (data.node.folder == 1) {
        this.closeSearching();
        this.viewFolder = data.node;
        this.stackNodes.push(originNode);
        originNode = data.node;

        refreshMainTreeContent(originNode.data.id);
      } else {
        openUrl([data.node.data.url], 'OPEN_NEW_TAB');
      }
    },
    toggleEditBar(type) {
      this.isOpenEditBar = type;
      const CHROME_PAGE_ZOOM = Number((document.body.clientWidth / window.innerWidth).toFixed(2));
      if (type) {
        $('#mainContent ul.ui-sortable').css({ zoom: CHROME_PAGE_ZOOM });
        $('#mainContent li>span.checkBox').css({ zoom: 1 / CHROME_PAGE_ZOOM });
        $('#mainContent li>span.fancytree-node').css({ zoom: 1 / CHROME_PAGE_ZOOM });
        $('#mainContent li span.reorderIcon').addClass('editing');
      } else {
        $('#mainContent ul.ui-sortable').css({ zoom: '' });
        $('#mainContent li>span.checkBox').css({ zoom: '' });
        $('#mainContent li>span.fancytree-node').css({ zoom: '' });
        $('#mainContent li span.reorderIcon').removeClass('editing');
      }
      if (!type && isUpdateBookmarkPosition) {
        isUpdateBookmarkPosition = false;
        doBookmarkSync();
      }
    },
    onChangeSelectedNodes() {
      this.selectedBookmarkCnt = selectedNodes.length;
      const activeNode = getNodeById(originNode.data.id);
      this.bookmarksViewCnt = activeNode && activeNode.children ? activeNode.children.length : 0;
    },
    onDeleteSelectedBookmark() {
      if (storageLocalApi.getItem(SYNC_STATUS) == 'SYNC_STARTED' || !!deleteTimeout) {
        return;
      }
      deleteMultiBookmarkOrTab(selectedNodes[0]);
    },
    onEditSelectAllBookmark(e) {
      if (this.selectedBookmarkCnt !== this.bookmarksViewCnt) {
        selectAllBookmark(true);
      } else {
        selectAllBookmark(false);
      }
      e.stopPropagation();
    },
    toggleEditSearchMode(mode) {
      this.onEditSearchResults = mode;
    },
    onChangeSelectedSearchItems() {
      this.selectedBookmarkCnt = selectedNodes.length;
      this.getBookmarkCounts();
    },
    onSelectAllSearchResults(e) {
      if (this.selectedBookmarkCnt !== this.bookmarksViewCnt) {
        Object.keys(resultsTree).forEach((key) => {
          selectAllSearchResults(resultsTree[key], true);
        });
      } else {
        Object.keys(resultsTree).forEach((key) => {
          selectAllSearchResults(resultsTree[key], false);
        });
      }
      e.stopPropagation();
    },
    getBookmarkCounts() {
      this.bookmarksViewCnt = 0;
      Object.keys(this.searchResults).forEach((key) => {
        this.bookmarksViewCnt += (this.searchResults[key] || []).length;
      });
    },
    onDeleteSelectedSearchItems() {
      deleteSearchItems();
    },
    undoDelete() {
      clearTimeout(deleteTimeout);
      removeNotice();
      if (this.notification && this.notification.type === 'search') {
        undoBookmarkSearch();
      } else {
        undoBookmarkDelete();
      }
    },
    onChangeSettingsBadge(value) {
      this.isShowSettingsBadge = value;
    },
    async noticeRemovePassSync() {
      await storageLocalApi.waitForAvailable();
      if (storageLocalApi.getItem(SAVE_PASSWORD_ENABLED) === '1' || storageLocalApi.getItem(AUTOFILL_PASSWORD_ENABLED) === '1') {
        var opt = {
          type: 'basic',
          iconUrl: 'assets/img/logo48x48.png',
          title: chrome.i18n.getMessage('PC_BR_HEADER_GET_THE_NEW_SAMSUNG_PASS_EXTENSION'),
          message: chrome.i18n.getMessage(
            'PC_BR_BODY_SAMSUNG_PASS_ISNT_PART_OF_THE_SAMSUNG_INTERNET_EXTENSION_ANYMORE_TO_KEEP_USING_SAMSUNG_PASS_TO_FILL_IN_USERNAMES_AND_PASSWORDS_ONLINE_GET_THE_SAMSUNG_PASS_EXTENSION',
          ),
          priority: 1,
          buttons: [{ title: chrome.i18n.getMessage('PC_BR_BUTTON_GET_THE_SAMSUNG_PASS_EXTENSION_40') }, { title: chrome.i18n.getMessage('IDS_BR_BUTTON_CANCEL') }],
        };
        storageLocalApi.removeItems([SAVE_PASSWORD_ENABLED, AUTOFILL_PASSWORD_ENABLED, EXTENSION_AES_CBC_KEY, EXTENSION_AES_CBC_IV, SERVER_CERT, SPASS_USER_ID, 'PASS_APP_STATUS']);
        const removePassNoticeId = 'notice-remove-pass';
        chrome.notifications.create(removePassNoticeId, opt, function () {});
        chrome.notifications.onButtonClicked.addListener((notificationId, buttonIndex) => {
          console.log(notificationId, buttonIndex);
          if (notificationId === removePassNoticeId && buttonIndex === 0) {
            if (getBrowserName().toUpperCase() === EDGE) {
              openUrl([PASS_EDGE_WEBSTORE_URL], OPEN_NEW_TAB);
            } else {
              openUrl([PASS_CHROME_WEBSTORE_URL], OPEN_NEW_TAB);
            }
          }
        });
      }
    },
  },
};
</script>

