<template>
  <div id="chrome-import-options" class="settingImport">
    <div class="import-options-container">
      <div>
        <span id="importChromeBookmark">{{ chrome.i18n.getMessage('IMPORT_CHROME_BOOKMARK_OPTIONS', [browserName]) }}</span>
      </div>
      <div class="import-options" v-if="visible">
        <app-radio-group :options="options" v-model="selected" @input="confirm" />
      </div>
    </div>
  </div>
</template>
<script>
import appRadioGroup from './common/app-radio-group.vue';

export default {
  name: 'ChromeImportOptions',
  components: {
    appRadioGroup,
  },
  props: {
    visible: Boolean,
  },
  data() {
    return {
      browserName: [],
      options: [
        { title: chrome.i18n.getMessage('IMPORT_OPTION_SELECT'), value: '1' },
        { title: chrome.i18n.getMessage('IMPORT_REPLACE_TEXT'), value: '2' },
        { title: chrome.i18n.getMessage('IMPORT_OVERWRITE_TEXT'), value: '3' },
      ],
      selected: '1',
    };
  },
  mounted() {
    this.browserName = getBrowserName();
    storageLocalApi.waitForAvailable().then(() => {
      if (storageLocalApi.getItem('IMPORT_OPTION') == null) {
        storageLocalApi.setItem('IMPORT_OPTION', '1');
      }
      this.selected = storageLocalApi.getItem('IMPORT_OPTION') || '1';
    });
  },
  methods: {
    confirm() {
      storageLocalApi.setItem('IMPORT_OPTION', this.selected);
    },
  },
};
</script>
