<template>
  <div id="content-settings" v-show="visible">
    <div id="settingMain" class="settingMain">
      <div id="settingMenuList" class="settingMenuList" v-show="!isOpenSyncSetting">
        <div class="sync-info">
          <div id="samsungAccount" class="settingAccount" @click="onAccountClick">
            <span class="menuItemText samsungAccountTxt">{{ chrome.i18n.getMessage('DREAM_BR_BODY_SAMSUNG_ACCOUNT') }}</span>
            <span id="settingAccountText" class="settingText">{{ settingAccountText }}</span>
          </div>
          <hr class="menuItemdivider" />
          <!-- <a id="menu2" class="menuItem" tabindex="2">
                          <span id="syncSamsungBookmarks" class="menuItemText"></span>
                          <span id="syncTime" class="settingText"></span>
                        </a> -->
          <a class="menuItem sync-status" @click="onClickSyncSettings">
            <div class="scloud">
              <span class="menuItemText syncWithSSCloudTxt">{{ chrome.i18n.getMessage('DREAM_BR_TMBODY_SYNC_WITH_SAMSUNG_CLOUD') }}</span>
              <span id="settingAccountText" class="settingText">{{ settingAccountText }}</span>
            </div>
            <div class="switchButton" v-if="visible" @click.stop>
              <app-switch-button v-model="syncStatus" @onChange="onChangeDataSyncSettings"></app-switch-button>
            </div>
          </a>
        </div>
        <ChromeImportOptions :visible="visible" />
        <div class="about">
          <div>
            <span class="about-title">{{ chrome.i18n.getMessage('DREAM_REMINDER_TMBODY_ABOUT_PS').replace('%s', chrome.i18n.getMessage('EXT_NAME')) }}</span>
          </div>
          <div class="version">
            <span @click="downloadLog">{{ chrome.i18n.getMessage('DREAM_CLOUD_HEADER_VERSION_PD').replace('%d', chrome.runtime.getManifest().version) }}</span>
          </div>
          <div class="ui-version">
            <span class="oneUIVersionTxt" @click="downloadBookmarkDb">{{ chrome.i18n.getMessage('DREAM_ST_TMBODY_ONE_UI_VERSION') }}</span>
            <span>5</span>
          </div>
          <div class="guide-link">
            <a id="menu3" class="contact-us" tabindex="3" @click="goContactUs">
              <span id="contactUs">{{ chrome.i18n.getMessage('CONTACT_US') }}</span>
            </a>
            <a id="menu4" class="help" tabindex="4" @click="goHelp">
              <span id="help">{{ chrome.i18n.getMessage('HELP') }}</span>
            </a>
            <a id="menu5" class="privacy-notice" tabindex="5" @click="goPrivacyNotice">
              <span id="privacyNotice">{{ chrome.i18n.getMessage('DREAM_HELP_OPT_PRIVACY_NOTICE_M_COLLECT') }}</span>
            </a>
          </div>
        </div>
      </div>
      <input type="file" id="bookmark-file-input" style="display: none" />
      <div class="sync-settings" v-show="isOpenSyncSetting && !isShowMoreInformation">
        <div class="on-synced">
          <div class="status" v-if="visible">
            <app-switch-button v-model="syncStatus" @onChange="onChangeDataSyncSettings"></app-switch-button>
          </div>
          <span id="syncTime" class="last-sync-info settingText"></span>
        </div>
        <div class="data-sync" :class="{ disabled: !syncStatus }">
          <div class="bookmark" v-if="visible">
            <span>{{ chrome.i18n.getMessage('IDS_BR_OPT_BOOKMARK') }}</span>
            <app-switch-button v-model="bookmarkSync" @onChange="onChangeBookmarkSyncSetting"> </app-switch-button>
          </div>
          <!-- <hr class="itemSettingDivider">
                  <div class="open-tabs">
                    <span>Synced tab</span>
                    <app-switch-button v-model="tabSync"></app-switch-button>
                  </div> -->
        </div>
        <div class="sync-method" :class="{ disabled: !syncStatus || !bookmarkSync }">
          <div id="menu2" class="sync-now" tabindex="2" @click="onSyncNow">
            <span id="syncNow" class="menuItemText">{{ chrome.i18n.getMessage('DREAM_HEALTH_BUTTON_SYNC_NOW_30') }}</span>
            <span id="syncedItems" class="settingText"></span>
          </div>
          <hr class="itemSettingDivider" />
          <div class="sync-using">
            <div class="sync-using-title">
              <span class="syncUsingTxt">{{ chrome.i18n.getMessage('DREAM_CLOUD_TMBODY_SYNC_USING') }}</span>
            </div>
            <hr class="itemSettingDivider" />
            <div class="method-options" v-if="visible">
              <app-radio-group :options="syncOptions" v-model="syncUsing" @input="setSyncUsing" />
            </div>
          </div>
        </div>
        <div class="dropdown-menu" v-if="isShowMoreOptions">
          <ul>
            <li>
              <span class="dropdown-item moreInformationTxt" @click="showMoreInformation">{{ chrome.i18n.getMessage('DREAM_CLOUD_OPT_MORE_INFORMATION_ABB') }}</span>
            </li>
          </ul>
        </div>
        <div class="overlay" v-if="isShowMoreOptions" @click="toggleMoreOptions"></div>
      </div>
      <div class="more-information-setting" v-if="isShowMoreInformation">
        <div class="syncing-work">
          <span>{{ chrome.i18n.getMessage('DREAM_CLOUD_BODY_HOW_DOES_SYNCING_WORK_Q') }}</span>
          <span>{{ chrome.i18n.getMessage('DREAM_CLOUD_BODY_DATA_FROM_YOUR_APPS_WILL_BE_SYNCED_WITH_SAMSUNG_CLOUD_IF_C') }}</span>
          <span>- {{ chrome.i18n.getMessage('DREAM_CLOUD_BODY_YOURE_SIGNED_IN_TO_YOUR_SAMSUNG_ACCOUNT') }}</span>
          <span>- {{ chrome.i18n.getMessage('DREAM_CLOUD_BODY_THE_APP_HAS_SYNC_TURNED_ON_IN_SETTINGS') }}</span>
        </div>
        <div class="information-synced">
          <span>{{ chrome.i18n.getMessage('DREAM_CLOUD_BODY_WHAT_INFORMATION_IS_SYNCED_Q') }}</span>
          <span>- {{ chrome.i18n.getMessage('DREAM_CLOUD_OPT_SAMSUNG_NOTES_C_NOTES_AND_OTHER_DATA') }}</span>
          <span>- {{ chrome.i18n.getMessage('PC_CLOUD_OPT_SAMSUNG_SETTINGS_C_PAIRED_GALAXY_BUDS') }}</span>
        </div>
        <span class="sync-note">
          **
          {{
            chrome.i18n.getMessage('DREAM_CLOUD_BODY_NOTES_AND_OTHER_CONTENT_LARGER_THAN_P1SS_P2SS_CANT_BE_SYNCED').replace(/%1|%2/gi, function (matched) {
              return matched == '%1' ? '1' : ' GB';
            })
          }}
        </span>
        <div class="divice-os">
          <span>{{ chrome.i18n.getMessage('DREAM_ST_BODY_DEVICE_AND_OS_DIFFERENCES') }}</span>
          <span>{{ chrome.i18n.getMessage('PC_CLOUD_BODY_THE_DATA_THAT_YOU_CAN_SYNC_MAY_DIFFER_BASED_ON_YOUR_COMPUTER_AND_SYSTEM_SOFTWARE') }}</span>
        </div>
      </div>
    </div>
    <div id="settingBottom" class="settingBottom">
      <div id="settingLogout" class="negativeButton" tabindex="5">
        <h2 id="logOut" class="logOutText">{{ chrome.i18n.getMessage('IDS_ST_SK3_SIGN_OUT') }}</h2>
      </div>
    </div>
  </div>
</template>

<script>
import appSwitchButton from '../common/app-switch.vue';
import appRadioGroup from '../common/app-radio-group.vue';
import ChromeImportOptions from '../ChromeImportOptions.vue';

export default {
  name: 'ContentSettings',
  components: {
    appSwitchButton,
    appRadioGroup,
    ChromeImportOptions,
  },
  data() {
    return {
      settingAccountText: '',
      syncStatus: true,
      bookmarkSync: true,
      tabSync: true,
      visible: false,
      isOpenSyncSetting: false,
      syncOptions: [],
      syncUsing: '1',
      headerSettingElm: document.getElementById('header-settings'),
      headerSyncSettingElm: document.getElementById('header-sync-settings'),
      headerMoreInfomation: document.getElementById('header-more-information'),
      isShowMoreOptions: false,
      isShowMoreInformation: false,
    };
  },
  mounted() {
    window.ContentSettings = this;
    storageLocalApi.waitForAvailable().then(() => {
      this.settingAccountText = storageLocalApi.getItem('ACCOUNT_NAME');
      this.onInit();
    });
  },
  methods: {
    onInit() {
      this.syncStatus = storageLocalApi.getItem(SYNC_ENABLED) !== false ? true : false;
      this.bookmarkSync = storageLocalApi.getItem(IS_BOOKMARK_SYNC_ON) !== false ? true : false;
      this.tabSync = storageLocalApi.getItem(IS_OPEN_TABS_SYNC_ON) !== false ? true : false;
      this.syncOptions = [
        { title: chrome.i18n.getMessage('PC_CLOUD_OPT_WI_FI_AND_ETHERNET_ONLY'), value: '1' },
        { title: chrome.i18n.getMessage('PC_CLOUD_OPT_WI_FI_ETHERNET_AND_MOBILE_DATA'), value: '2' },
      ];
      this.syncUsing = storageLocalApi.getItem(SYNC_USING_SETTING) || '1';
      this.headerSettingElm = document.getElementById('header-settings');
      this.headerSyncSettingElm = document.getElementById('header-sync-settings');
      this.headerMoreInfomation = document.getElementById('header-more-information');
    },
    open() {
      this.visible = true;
    },
    close() {
      this.visible = false;
    },
    onClickSyncSettings() {
      this.isOpenSyncSetting = true;
      this.headerSettingElm.style.display = 'none';
      this.headerSyncSettingElm.style.display = 'flex';
    },
    closeSyncSettings() {
      this.isOpenSyncSetting = false;
      this.headerSettingElm.style.display = 'flex';
      this.headerSyncSettingElm.style.display = 'none';
    },
    toggleMoreOptions() {
      this.isShowMoreOptions = !this.isShowMoreOptions;
    },
    showMoreInformation() {
      this.toggleMoreOptions();
      this.isShowMoreInformation = true;
      this.headerMoreInfomation.style.display = 'flex';
      this.headerSyncSettingElm.style.display = 'none';
      this.headerSettingElm.style.display = 'none';
    },
    closeMoreInformation() {
      this.isShowMoreInformation = false;
      this.headerMoreInfomation.style.display = 'none';
      this.headerSyncSettingElm.style.display = this.isOpenSyncSetting ? 'flex' : 'none';
      this.headerSettingElm.style.display = this.isOpenSyncSetting ? 'none' : 'flex';
    },
    closeSettingsView() {
      this.isOpenSyncSetting = false;
      this.isShowMoreInformation = false;
      this.headerSyncSettingElm.style.display = 'none';
      this.headerMoreInfomation.style.display = 'none';
      this.headerSettingElm.style.display = 'flex';
      handleSettingsBack();
    },
    onAccountClick() {
      SignoutModal.toggle();
    },
    onSyncNow() {
      if (navigator.onLine) {
        syncNow();
      }
    },
    goContactUs() {
      onClickContactUs();
    },
    goHelp() {
      onClickHelp();
    },
    goPrivacyNotice() {
      onClickPrivacyNotice();
    },
    onChangeDataSyncSettings() {
      storageLocalApi.setItem(SYNC_ENABLED, this.syncStatus);
    },
    onChangeBookmarkSyncSetting() {
      storageLocalApi.setItem(IS_BOOKMARK_SYNC_ON, this.bookmarkSync);
    },
    setSyncUsing() {
      storageLocalApi.setItem(SYNC_USING_SETTING, this.syncUsing);
    },
    downloadBookmarkDb() {
      if (EXT_ENVIRONMENT === 'qa') {
        DB.Query.bookmarks({}).then((result) => {
          const dbString = JSON.stringify(result);
          if (dbString.length) {
            var bookmarkDbAElement = document.createElement('a');
            bookmarkDbAElement.setAttribute('href', `data:text/plain;charset=utf-8,${encodeURIComponent(dbString)}`);
            bookmarkDbAElement.setAttribute('download', `ExtensionDb_Bookmark_Sync-${Date.now()}.json`);
            bookmarkDbAElement.style.display = 'none';
            document.body.appendChild(bookmarkDbAElement);
            bookmarkDbAElement.click();
            document.body.removeChild(bookmarkDbAElement);
          }
        });
      }
    },
    downloadLog() {
      chrome.runtime.sendMessage(
        {
          cmd: 'GET_BACKGROUND_LOG',
        },
        (logs) => {
          const passLogString = JSON.parse(logs.pass).join('\n');
          if (passLogString.length) {
            var passLogElement = document.createElement('a');
            passLogElement.setAttribute('href', `data:text/plain;charset=utf-8,${encodeURIComponent(passLogString)}`);
            passLogElement.setAttribute('download', `ExtensionLog-PassSync-${Date.now()}.txt`);
            passLogElement.style.display = 'none';
            document.body.appendChild(passLogElement);
            passLogElement.click();
            document.body.removeChild(passLogElement);
          }

          const syncLogString = JSON.parse(logs.sync).join('\n');
          var syncLogElement = document.createElement('a');
          syncLogElement.setAttribute('href', `data:text/plain;charset=utf-8,${encodeURIComponent(syncLogString)}`);
          syncLogElement.setAttribute('download', `ExtensionLog-CloudSync-${Date.now()}.txt`);
          syncLogElement.style.display = 'none';
          document.body.appendChild(syncLogElement);
          syncLogElement.click();
          document.body.removeChild(syncLogElement);
        },
      );
    },
  },
};
</script>
<style scoped src="./ContentSettings.css"></style>
