import Vue from 'vue';
import App from './App.vue';
import store from './popup/store';

Vue.config.productionTip = false;

const EventBus = new Vue();
window.EventBus = EventBus;

Vue.directive('focus', {
  inserted: function (el) {
    el.focus();
  },
});

Vue.prototype.chrome = window.chrome;

new Vue({
  store,
  render: (h) => h(App),
}).$mount('#app');
